html {
  font-size: 10px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  color: #60605a; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.btn {
  margin-left: 10px;
  background: none;
  border: 1px solid transparent;
  color: #56d9f5;
  padding: 5px;
  font-weight: 700; }
  .btn:hover {
    cursor: pointer;
    border: 1px solid #56d9f5;
    border-radius: 3px; }

.sidebar .form__select .arrow-closed, .sidebar .form__select .arrow-open {
  position: absolute;
  width: 10px;
  height: 10px;
  border-width: 5px;
  border-style: solid;
  box-sizing: border-box;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.grid {
  max-width: 1040px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "sidebar posts";
  grid-template-columns: 1fr 3fr;
  grid-column-gap: 20px;
  padding: 20px;
  font-size: 1.4rem; }
  @media (max-width: 780px) {
    .grid {
      grid-template-areas: "posts";
      grid-template-columns: 1fr; } }

.sidebar {
  grid-area: sidebar;
  background-color: #f5f6f1;
  border-radius: 3px; }
  @media (max-width: 780px) {
    .sidebar {
      display: none; } }
  .sidebar .form__control {
    padding: 10px;
    position: relative;
    margin-bottom: 10px; }
  .sidebar .form__input {
    width: 100%; }
  .sidebar .form__error {
    position: absolute;
    top: 100%;
    left: 10px;
    color: red;
    font-size: 12px; }
  .sidebar .form__select {
    background-color: white;
    border: 1px solid #60605a;
    border-radius: 3px;
    padding: 5px;
    position: relative;
    box-sizing: border-box; }
    .sidebar .form__select .select__menu {
      position: absolute;
      background-color: white;
      top: calc(100% - 2px);
      left: -1px;
      width: calc(100% + 2px);
      padding: 5px;
      box-sizing: border-box;
      z-index: 2;
      border-width: 0 1px 1px 1px;
      border-style: solid;
      border-color: transparent #60605a #60605a #60605a; }
    .sidebar .form__select .arrow-closed {
      border-color: #60605a transparent transparent transparent; }
    .sidebar .form__select .arrow-open {
      border-color: transparent transparent #60605a transparent; }
  .sidebar .form__label {
    color: #33312f;
    font-size: 1.6rem;
    font-weight: 700;
    margin-bottom: 5px;
    display: block; }
  .sidebar .form .input {
    display: none; }
    .sidebar .form .input__label {
      font-size: 1.4rem;
      margin-left: 10px;
      font-weight: 600; }
    .sidebar .form .input__wrap {
      margin: 5px;
      display: flex;
      justify-content: flex-start;
      align-items: center; }
    .sidebar .form .input__tick {
      width: 12px;
      height: 12px;
      box-sizing: border-box;
      border: 1px solid #60605a;
      border-radius: 3px; }
      .sidebar .form .input__tick.checked {
        background-position: center center;
        background-repeat: no-repeat;
        background-image: url(/postss/static/media/checked.161834d6.svg); }
      .sidebar .form .input__tick-rounded {
        border-radius: 50%; }
        .sidebar .form .input__tick-rounded.checked {
          background-image: url(/postss/static/media/circle.91f81a03.svg);
          background-size: 60% 60%; }
      .sidebar .form .input__tick-green.checked {
        background-image: url(/postss/static/media/checked-green.96e6a05d.svg); }

.posts {
  grid-area: posts; }
  .posts .post {
    border: 1px solid #dedede;
    border-radius: 3px;
    margin: 5px 10px;
    padding: 20px; }
    .posts .post__title {
      color: #56d9f5; }

.options {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 30px; }
  .options .option {
    white-space: nowrap; }
    .options .option-blue .option__key, .options .option-blue .option__value {
      color: #56d9f5; }
    .options .option__key {
      color: #b2b2b2;
      display: inline-block; }
      .options .option__key:first-letter {
        text-transform: uppercase; }
    .options .option__value {
      color: #60605a;
      font-weight: 600;
      margin-left: 4px; }
      .options .option__value-background {
        background-color: #f5f6f1;
        display: inline-block;
        padding: 5px;
        border-radius: 3px; }
    .options .option__separator {
      display: inline-block;
      width: 1px;
      border-right: 1px solid #dedede;
      height: 10px;
      margin: 0 5px; }
    .options .option__stars {
      display: inline-flex;
      margin-left: 5px; }
    .options .option__star {
      display: inline-block;
      width: 20px;
      height: 20px;
      background-image: url(/postss/static/media/star.88688cf0.svg);
      margin: 0 2px; }
      .options .option__star-colored {
        background-image: url(/postss/static/media/star-colored.166e2800.svg); }

